import React, { Component } from "react";
import './index.scss'
class GoodsImg extends Component{
  constructor(props){
    super(props)
  }
  render(){
    return(
      <React.Fragment>
        <div className="big-img">
          <img src="" alt=""/>
        </div>
        <ul className="img-wrap">
          {
            [1,2,3,4,5,6].map((item,index)=>{
              return(
                <li className="smal-img" key={index}>
                  <img src="" alt=""/>
                </li>
              )
            })
          }
         
        </ul>
      </React.Fragment>
    )
  }
}

export default GoodsImg