import React, { Component } from "react";
import { connect } from "react-redux";
import { Button,Icon,InputNumber  } from 'antd';
import GoodsImg from "../goodsImg"
import GoodsBottom from "../goodsBottom"
import './index.scss'
class FloorDetail extends Component{
  constructor(props){
    super(props)
  }
  render(){
    return(
      <div className="container floor-detail">
        <div className="fd-goods-info">
          <div className="fd-info-left">
            <GoodsImg></GoodsImg>
          </div>
          <div className="fd-info-right">
            <h5 className="goods-name">马瑞利5W-40全合成发动机油SN级 4升装 FR-X101</h5>
            <div className="right-main">
              <div className="price-info">
                <span className="price">
                  <i>售价：</i>
                  <span className="price-color">￥<i>210.00</i></span>
                </span>
                <span className="goods-number">
                  <i>配件编号：</i>
                  <i className="number-color">941992403501</i>
                </span>
              </div>
              <div className="good-active">
                <p className="active-tip">本商品参加以下活动，可在购物车中选择其中一个参加</p>
                <div className="active-single">
                  <p className="active-logo">打折</p>
                  <div className="active-desc">
                    <h6>马瑞利全合成机油九折促销</h6>
                    <p>5月11日至5月31日期间，买瑞利合成机油可享受九折优惠！</p>
                  </div>
                </div>
                <div className="active-logo-wrap clearfix">
                  <span className="active-logo">打折</span>
                  <span className="active-logo">满赠</span>
                  <span className="active-more">
                  更多活动
                  <svg class="icon" aria-hidden="true">
                      <use xlinkHref="#icon-xiala1"></use>
                    </svg>
                  </span>
                </div>
                {
                  [1].length?(
                    <div className="other-active">
                      <div className="active-single">
                        <p className="active-logo">打折</p>
                        <div className="active-desc">
                          <h6>马瑞利全合成机油九折促销</h6>
                          <p>5月11日至5月31日期间，买瑞利合成机油可享受九折优惠！</p>
                        </div>
                      </div>
                    </div>
                  ):null
                }
                
              </div>
              <div className="goods-info">
                <div className="goods-info-list">
                  <span className="label-name">配 送 至</span>
                  <span className="list-info">浙江 杭州 拱墅区
                    <svg class="icon xiala" aria-hidden="true">
                      <use xlinkHref="#icon-xiala1"></use>
                    </svg>
                  </span>
                  <span>有货</span>
                </div>
                <div className="goods-info-list">
                  <span className="label-name">发货仓库</span>
                  <span className="list-info">
                    <i className="active-icon">荐</i>
                    <i className="active-icon">优</i>
                    <span>武汉中心仓库</span>
                    <svg class="icon xiala" aria-hidden="true">
                      <use xlinkHref="#icon-xiala1"></use>
                    </svg>
                  </span>
                  <span>剩余156</span>
                </div>
                <div className="goods-info-list">
                  <span className="label-name">数量</span>
                  <InputNumber min={1} max={10} defaultValue={3}/>
                </div>
              </div>
              <Button className="add-shop-btn" type="primary">加入购物车</Button>
            </div>
          </div>
        </div>
        <GoodsBottom></GoodsBottom>
      </div>
    )
  }
}
export default FloorDetail